:root {
  --black:#222222;
  --blue:#005FA3;
  --gray:#EBEBEB;
}

.btn {
  display: flex;
  border: none;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 21px 56px;
  background: var(--blue);
  color: #fff;
  font-weight: 400;
  font-size: 18px;
}
.btn_size-m {
  padding: 19px 38px;
}
@media (max-width: 540px) {
  .btn {
    padding: 15px 40px;
    font-size: 14px;
  }
}

.slider-pagination {
  display: flex;
  align-items: center;
}
.slider-pagination > span {
  cursor: pointer;
  display: block;
  width: 10px;
  height: 10px;
  background: #fff;
  opacity: 0.4;
  border-radius: 50%;
  transition: 0.3s ease-in-out;
  transition-property: opacity;
}
.slider-pagination > span:not(:last-child) {
  margin-right: 40px;
}
.slider-pagination > span:hover {
  opacity: 1;
}
@media (max-width: 540px) {
  .slider-pagination > span:not(:last-child) {
    margin-right: 30px;
  }
}
.slider-nav {
  display: flex;
}
.slider-nav button {
  flex: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  transition: 0.3s ease-in-out;
  transition-property: background;
}
.slider-nav button:last-child span {
  transform: scaleX(-1);
}
.slider-nav button:hover {
  background: rgba(255, 255, 255, 0.4);
}
.slider-nav button span {
  display: block;
  width: 15px;
}
@media (max-width: 540px) {
  .slider-nav button {
    width: 50px;
    height: 50px;
  }
}
.slider-nav-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.slider_blue .slider-nav button {
  background: rgba(0, 95, 163, 0.2);
}
.slider_blue .slider-nav button:hover {
  background: #005fa3;
}
.slider_blue .slider-pagination span {
  background: var(--blue);
}

