* {
  box-sizing: border-box;
  min-width: 0;
  min-height: 0;
  margin: 0;
  padding: 0;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html.is-locked {
  height: calc(var(--window-height) - 1px);
  overflow: hidden;
}
html.is-locked body {
  overflow: visible;
  position: fixed;
  top: 0;
  left: 0;
  height: var(--window-height);
}

html {
  overflow-x: hidden;
}

html, body {
